// About.vue
<template>
  <v-container fluid>
    <v-layout row wrap>
      <v-flex xs12>
        <h3 class="headline-mobile headline white--text"> </h3>
        <h4 class="caption">We are objective</h4>
        <p>
              , kosdak  ,     
          <br>   ,   ,     ,      
          <br>Active X        
          <br>      ,     
          <br>    ,  X  
          <br> 2  
          <br>XXX  , XXX  
          <br>   ~~  
        </p>
        <p>   ,       .</p>
      </v-flex>
    </v-layout>

    <!-- <v-layout row wrap sticky-group>
      <v-flex xs12 v-for="n in 10" :key="n">
        <h3 class="headline white--text sticky">headline.{{n}}</h3>
        <h4 class="caption sticky-c">caption.{{n}}</h4>
        <p>{{dumyText}}</p>
      </v-flex>
    </v-layout>-->

    <Scrollama
      :debug="true"
      :offset=".5"
      :progress="true"
      @step-progress="({ progress }) => (currStepProgress = progress)"
      @step-enter="stepEnterHandler"
      @step-exit="stepExitHandler"
    >
      <div class="graphic" slot="graphic">{{ currStepId }}/{{ Math.round(currStepProgress * 100) }}%</div>
      <div
        v-for="step in steps"
        :key="step.no"
        :data-step-id="step.id"
        class="step"
        :class="{'is-active': step.id == currStepId}"
        :style="_scale"
      >
        <p>STEP {{step.id}} {{ currStepProgress }}%</p>
      </div>
    </Scrollama>
  </v-container>
</template>

<script>
import "intersection-observer";
import Scrollama from "vue-scrollama";

export default {
  name: "App",
  components: {
    Scrollama
  },
  data() {
    return {
      dumyText:
        "Our talented designers, developers and project managers not only share the same passion for creating beautiful websites and products, but also capable of offering scalable solutions for all business types.",
      currStep: null,
      currStepId: null,
      currStepProgress: null,
      steps: [
        { id: 1 },
        { id: 2 },
        { id: 3 },
        { id: 4 },
        { id: 5 },
        { id: 6 },
        { id: 7 }
      ]
    };
  },
  computed: {
    headerStyle() {
      return {
        transform: `translateY(-${25 * this.currStepProgress}%) scale(${1 -
          this.currStepProgress})`
      };
    },
    _opacity() {
      return {
        opacity: this.currStepProgress
      };
    },
    _scale() {
      return {
        transform: 1 //`scale(${this.currStepProgress})`
      };
    }
  },
  methods: {
    stepEnterHandler({ element, direction, index }) {
      // console.log("in:", { element, direction, index });
      this.currStepId = element.dataset.stepId;
    },
    stepExitHandler({ element, direction, index }) {
      // console.log("out:", { element, direction, index });
      this.currStepId = null;
    }
  }
};
</script>
<style src="vue-scrollama/dist/vue-scrollama.css"></style>
<style lang="scss">
.sticky-group {
  .xs12 {
    height: calc(100vh - 56px);
    // height: calc(100% + 16px);
  }
  .sticky {
    display: flex;
    justify-content: center;
    align-items: center;
    position: sticky;
    font-weight: 400;
    background: #313131;
    color: #fff;
    margin: 0 -16px;
    padding: 0 16px;
    height: 56px;
    top: 56px;
  }
  .sticky-c {
    display: flex;
    justify-content: start;
    align-items: center;
    position: sticky;
    color: #fff;
    height: 56px;
    top: 56px;
  }
}
// additions and overrides of DOM elements vue-scrollama sets up
.scrollama-container {
  .scrollama-graphic {
    z-index: 1;
    top: 56px;
    width: 100%;
    display: flex;
    .graphic {
      width: 100%;
      height: 56px;
      background: rgba(255, 255, 255, 0.5);
    }
  }
  .scrollama-steps {
    z-index: 0;
    .step {
      display: flex;
      justify-content: start;
      align-items: center;
      columns: #f00;
      height: 100vh;
      background: #fff;
      transition: 600ms cubic-bezier(0.59, 0.12, 0.34, 0.95);
      opacity: 0;
      &.is-active {
        transition: 300ms cubic-bezier(0.59, 0.12, 0.34, 0.95);
        background: rgb(0, 119, 255);
        opacity: 1;
      }
    }
  }
}
// your elements styles
</style>